  }),
  methods: {
    getCol() {
      if (this.windowWidth > 1200) {
        return true;
      } else {
        return false;
      }
    },

    isMobile() {
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        return true;
      } else {
        return false;
      }
    },
  },
  watch: {
    group() {
      this.drawer = false;
    },
  },
};
